define('ember-htmlbars/helpers/if_unless', ['exports', 'ember-metal/core', 'ember-views/streams/should_display'], function (exports, Ember, shouldDisplay) {

  'use strict';

  exports.ifHelper = ifHelper;
  exports.unlessHelper = unlessHelper;

  /**
  @module ember
  @submodule ember-htmlbars
  */

  function ifHelper(params, hash, options) {
    return ifUnless(params, hash, options, shouldDisplay['default'](params[0]));
  }

  /**
    @method unless
    @for Ember.Handlebars.helpers
  */
  function unlessHelper(params, hash, options) {
    return ifUnless(params, hash, options, !shouldDisplay['default'](params[0]));
  }

  function ifUnless(params, hash, options, truthy) {
    
    
    if (truthy) {
      if (options.template.yield) {
        options.template.yield();
      } else {
        return params[1];
      }
    } else {
      if (options.inverse.yield) {
        options.inverse.yield();
      } else {
        return params[2];
      }
    }
  }

});